<template>
  <div>
  <div>
    <fieldset>
      <legend>{{selectRow.policyName}}</legend>
      <div>
        <div class="left">
          ID{{selectRow.policyId}}
          :
          <el-tag
            size="mini"
            v-for="item in selectRow.tagsList"
            :key="item.tagId"
            :type= "item.dataSource === 2 ? 'danger' : 'success'"
          >{{item.tagName}}</el-tag>
        </div>
        <div class="right">
          <el-button size="small" type="success" @click="goBack"></el-button>
        </div>
      </div>
    </fieldset>
    <!-- authority -->
    <div class="TopNav">
      <div class="left">
        <el-button-group>
          <el-button
            type="primary"
            size="small"
            @click="handleAdd"
            v-permission="'hoder:crowd:add'"
          >
            <a class="fa fa-plus" style="color: white;"></a>
          </el-button>
        </el-button-group>
      </div>
      <div class="right">
        <!-- form search -->
        <el-form
          :inline="true"
          :model="searchForm"
          ref="searchForm"
          @submit.native.prevent="submitForm"
          shiro:hasPermission="sysAdministrative:role:search"
        >
          <el-form-item label prop="crowdName">
            <el-input v-model="searchForm.crowdName" style="width: 200px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" icon="search" @click="submitForm"></el-button>
            <el-button type="primary" size="small" @click="handleReset">
              <a class="fa fa-refresh" style="color: white;"></a> 
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- talbe -->
    <el-table ref="myTable" :data="tableData" style="width: 100%;" stripe border>
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" class="demo-table-expand">
            <el-form-item label=":">
              <span>{{ props.row.crowdName }}</span>
            </el-form-item>
            <el-form-item label=":">
              <div
                v-for="(item, index) in JSON.parse(props.row.rulesJson).rules"
                :key="index"
                class="detail"
              >
                <div v-if="index>0" class="label-or-space"></div>

                <div class="label-ground">(
                  <div
                    v-for="(childItem,childItemIndex) in item.rules"
                    :key="childItem.tagId"
                    class="label-item"
                  >
                    <div v-if="childItemIndex>0" class="label-or-space"></div>
                    <span class="txt">{{ childItem.categoryName }}</span>
                    <span class="sel">{{ childItem.operator }}</span>
                    <span class="in">
                      <!-- <el-date-picker
                        v-if="childItem.tagType === 'time'"
                        class="datetime-format"
                        v-model="childItem.tagCode"
                        type="date"
                        placeholder=""
                      ></el-date-picker> -->
                      <span >{{ childItem.value }}</span>
                    </span>
                  </div>)
                </div>
              </div>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="crowdId" label="ID" width="50"></el-table-column>
      <el-table-column prop="crowdName" label="" width="200"></el-table-column>
      <el-table-column prop="priority" label="" width="90"></el-table-column>
      <el-table-column prop="remark" label="" width="90"></el-table-column>
      <el-table-column prop="forcastStatus" label="" width="80">
          <template scope="scope">
              <span type="text" v-if="scope.row.forcastStatus == 1"></span>
              <span type="text" v-if="scope.row.forcastStatus == 2"></span>
              <el-button type="text" v-if="scope.row.forcastStatus == 3" @click="showCountResult(scope.row.crowdId)"></el-button>
          </template>
      </el-table-column>
      <el-table-column prop="createTime" label="" width="180">
        <template scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right">
        <template scope="scope">
          <el-button-group>
            <el-button
              size="small"
              type="primary"
              v-permission="'hoder:crowd:edit'"
              @click="edit(scope.row)"
            ></el-button>
            <el-button
              size="small"
              type="info"
              v-permission="'hoder:crowd:del'"
              @click="del(scope.row)"
            ></el-button>
            <el-button
              size="small"
              type="warning"
              @click="estimateType(scope.row)"
              :disabled="scope.row.forcastStatus != 1"
            ></el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <!-- pagination -->
    <!-- pagination -->
    <div align="right">
      <pagination
        v-bind:currentpage="currentPage"
        v-bind:pagesize="pageSize"
        v-bind:totalcount="totalCount"
        @handle-size-change="handleSizeChange"
        @handle-current-change="handleCurrentChange"
      ></pagination>
    </div>
  </div>
  <!--  -->
  <el-dialog :visible.sync="showEstimate">
    <el-checkbox-group v-model="estimateValue">
      <el-checkbox v-for="(item,index) in estimateItems" :value="index" :label="index" :disabled="index==0">{{item}}</el-checkbox>
    </el-checkbox-group>
    <span slot="footer" class="dialog-footer">
    <el-button @click="showEstimate = false"> </el-button>
    <el-button type="primary" @click="handleEstimate"></el-button>
  </span>
  </el-dialog>
  <!--  -->
   <el-dialog :visible.sync="showResult" title="">
       <div>{{totalUser}}</div>
       <div>{{total1}}</div>
       <div>openId{{total2}}</div>
       <div>openId{{total3}}</div>
   </el-dialog>
  </div>
</template>
<script>
import _ from "lodash";
export default {
  data() {
    return {
      // 
      tableData: [],
      //
      criteria: {
        //  policyId:selectRow.policyId
      },
      // 
      searchForm: {
        crowdName: ""
      },
      // 
      // editFormVisible: false,// 
      // :pageSize
      pageSize: 10,
      // :pageNum
      currentPage: 1,
      totalCount: 1,
      // 
      start: 1,
      estimateItems: [],
      showEstimate: false,
      estimateValue: ['0'],
      estimateId: '',
        showResult: false,
        total1: '',
        total2: '',
        total3: '',
        totalUser: '',
        total4: ''
    };
  },
  props: ["selectRow"],
  created() {
    this.loadData();
  },
  methods: {
    goBack() {
      this.$emit("goBack");
    },
    tagsList(row) {},
    callback(data, successMsg) {
        this.loadData();
    },
    handleAdd() {
      this.$emit("addCrowd");
    },
    edit(row) {
      this.$emit("addCrowd", row.crowdId);
    },
    del(row) {
      var id = row.crowdId;
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$service.strategyCrowdDel({ crowdId: id},"").then(()=>{
            this.loadData();
          })
        }).catch(function(){

        })
    },
      // 
    estimateType(row) {
        this.estimateId = row.crowdId
        this.showEstimate = true
        this.$service.getEstimateType().then((data) => {
            this.estimateItems = data
        })
    },
      // 
    handleEstimate () {
        let calIdType = this.estimateValue.map((item) => item).join(',')
        this.$service.estimatePeople({crowdId: this.estimateId,calIdType: calIdType},"").then(
            (data) => {
                this.showEstimate = false
                this. loadData()
            }
        )
    },
      // 
    showCountResult (id) {
        const crowdId = id
        this.showResult = true
        this.$service.estimateResult({crowdId: crowdId}).then((data) => {
            this.total1 = data[0].total1 === null ? '': data[0].total1
            this.total2 = data[0].total2 === null ? '': data[0].total2
            this.total3 = data[0].total3 === null ? '': data[0].total3
            this.totalUser = data[0].totalUser === null ? '': data[0].totalUser
        })
    },
    // 
    loadData: function() {
      this.criteria["pageNum"] = this.currentPage;
      this.criteria["pageSize"] = this.pageSize;
      this.criteria.policyId = this.selectRow.policyId;
      this.$service.viewCrowd(this.criteria).then(data => {
        this.tableData = data.pageInfo.list;
        this.totalCount = data.pageInfo.total;
      });
    },
    // , 1020,val=20
    handleSizeChange: function(val) {
      this.pageSize = val;
      this.loadData();
    },
    // , 12,val=2
    handleCurrentChange: function(val) {
      this.currentPage = val;
      this.loadData();
    },
    // ,
    submitForm: function() {
      var _this = this;
      this.$refs.searchForm.validate(function(result) {
        if (result) {
          _this.criteria = _this.searchForm;
          _this.loadData();
        } else {
          return false;
        }
      });
    },
    // 
    handleReset: function() {
      this.$refs.searchForm.resetFields();
    },
    // 
    handleDetail: function(index, row) {
      var id = row.id;
      // todo: 
    }
  }
};
</script>
